}

%flex-row {
  @include flex-direction(row);
}

%flex-row-reverse {
  @include flex-direction(row-reverse);
}

%absFlexCenter {
  @include flexbox;
  @include justify-content(center);
  @include align-items(center);
}

%horizontalFlexCenter {
  @include flexbox;
  @include justify-content(center);
}

%verticalFlexCenter {
  @include flexbox;
  @include align-items(center);
}

