/* Modern Slider Area  */
.im_modern_slider {
  .slide {
    &.slide-style-2 {
      .inner {
        h1 {
          &.title {
            color: #ffffff;
            font-size: 70px;
            font-weight: 600;
            position: relative;
            margin-bottom: 30px;
