              margin-bottom: 20px;
            }
            &::before {
              position: absolute;
              content: "";
              background: #fff;
              width: 100px;
              height: 2px;
              bottom: 0;
              left: 50%;
              transform: translateX(-50%);
            }
          }
        }
        p {
          &.description {
            color: #fff;
            font-size: 28px;
            line-height: 40px;
            margin: 0;
            padding: 0 16%;
            font-weight: 300;
            letter-spacing: 2px;
            @media #{$md-layout} {
              font-size: 20px;
            }
          }
          &.description_style-2 {
            color: #fff;
            font-size: 19px;
            line-height: 40px;
            margin: 0;
            padding: 0 23%;
            font-weight: 400;
            letter-spacing: 1px;
            opacity: 0.8;
            @media #{$md-layout} {
              padding: 0;
            }
            @media #{$sm-layout} {
              padding: 0;
            }
          }
        }
        .slide-btn {
          @media #{$sm-layout} {
            margin-top: 30px;
          }
        }
        .btn-default {
          &:hover {
            color: #ffffff;
            border-color: #ffffff;
          }
        }
      }
    }
  }
}

